@import "./utils";

html {
  @include fontAvenir;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: map-get($colors, night);
}

body {
  @include resetMargin;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

h1 {
  @include fontAvenirBlack;
}

// Place footer in bottom page

.current-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  .main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
  }
}

// backgrounds

.bg-dark {
  background-color: map-get($colors, bgDark);
}
.bg-grey {
  background-color: map-get($colors, bgGrey);
}
.bg-green {
  background-color: map-get($colors, grass);
}
.bg-purple {
  background-color: map-get($colors, cassis);
}

// Borders

.border-green {
  border-color: map-get($colors, grass);
}
.border-purple {
  border-color: map-get($colors, cassis);
}

// hide informations only intended for screen readers

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important; /* 1 */
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important; /* 2 */
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important; /* 3 */
}

// Global forms

.input-wrapper {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-bottom: 1rem;
}

.input-wrapper label {
  font-weight: bold;
}

.input-wrapper input {
  padding: 5px;
  font-size: 1.2rem;
}

.input-error-message {
  height: 1rem;
  margin: 0;
  margin-top: 0.2rem;
  font-style: italic;
  color: map-get($colors, blood);
  font-size: map-get($sizes, mini);
}

.submit-message {
  min-height: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
  color: map-get($colors, grass);
  font-size: map-get($sizes, xs);
}

.submit-message--error {
  min-height: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
  color: map-get($colors, blood);
  font-size: map-get($sizes, xs);
}
